@media screen and (max-width: 500px){
    .App {
        transition: all .2s;
        min-height: 100vh;
    }
    .translated{
        transform: translate(100%, 0);
    }
    .not-translated{
        transform: translate(0);
    }
    header{
        nav#desktop{
            display: none;
        }
        nav#mobile{
            display: flex;
            flex-direction: row-reverse;
            width: 200%;
            justify-content: flex-start;
            align-items: flex-end;
            align-content: flex-start;
            margin-left: -100%;
            background-color: white;
            -webkit-box-shadow: 0px 4px 2px 0px rgba(14, 13, 13, 0.25);
            -moz-box-shadow: 0px 4px 2px 0px rgba(14, 13, 13, 0.25);
            box-shadow: 0px 4px 2px 0px rgba(14, 13, 13, 0.25);
            div.navigation{
                width: 50%;
                display: flex;
                flex-direction: column;
                padding:0;
                div{
                    height: 15vh;
                    width: 100%;
                    display: flex;
                    justify-content: space-between;
                    padding-left: 4vw;
                    flex-shrink: 0;
                    h1{
                        margin: 0;
                        font-size: 10.5vw;
                        font-family: "Titillium Web", sans-serif;
                        font-weight: 900;
                        width: 40%;
                        line-height: 9vw;
                        text-transform: uppercase;
                    }
                }
                
                ul{
                    width: 100%;
                    li{
                        font-family: 'Titillium Web', sans-serif;
                        font-weight: 900;
                        margin-top: 3vw;
                        list-style: none;
                        text-transform: uppercase;
                        font-size: 8vw;
                        color :#fe6600
                    }
                    div{
                        align-items: baseline;
                        height: auto;
                        ul{
                            a{
                                color: black
                            }
                            li{
                                margin: 0;
                                font-weight: 600;
                                font-style: italic;
                                text-transform: capitalize;
                                outline: thick;
                                font-size: 6vw;
                            }   
                        }
                    }
                }
            }
            div{
                display: flex;
                height: 15vh;
                align-items: center;
                justify-content: space-between;
                width: 50%;
                padding: 0 5%;
                button{
                    height: 10vw;
                    width: 10vw;
                    background-color: white;
                    border: 0;
                    padding: 0;
                    img{
                        width: 100%;
                        height: 100%;
                    }
                }
                button:focus{
                    outline: none;
                }
                a{
                    text-decoration: none;
                    img{
                        height: 10vw;
                        width: 10vw;
                    }
                    img.logo{
                        width: 30vw;
                        height: 25vw;
                        object-fit: cover;
                    }
                }
                
            }
            
        }
    }
    
    main {
        img#banner-mobile {
            width: 100%;
            height: 80vh;
            object-fit: cover;
        }

        img#banner-desktop{
            display: none;
        }
        .title_block{
            width: 90%;
            margin: 0 auto;
            padding: 0 0 1vw;
            margin: 6vw auto 4vw;
            border-bottom: 3vw #fe6600 solid;
            h2{
                margin: 0;
                font-size: 7.5vw;
                font-family: 'Titillium Web', sans-serif;
                font-weight: 900;
                text-transform: uppercase;
                color :#fe6600
            }
            input{
                border: 0;
                background-color: transparent;
                font-size: 7.5vw;
                font-family: 'Titillium Web', sans-serif;
                height: 7vw;
                padding: 0;
                margin: 0;
                font-weight: 900;
                text-transform: uppercase;
                color :#fe6600
            }
            input::placeholder{
                font-size: 7.5vw;
                text-transform: uppercase;
                font-family: 'Titillium Web', sans-serif;
                font-weight: 900;
                color :#fe6600
            }
            input:focus{
                outline: none;
            }
        }
    
        .article_list {
            width: 100%;
            height: 50vh;
            margin: 3vw auto;
            display: flex;
            flex-direction: column;
            background-color: white;
            align-items: center;
            -webkit-box-shadow: 0px 4px 2px 0px rgba(14, 13, 13, 0.25);
            -moz-box-shadow: 0px 4px 2px 0px rgba(14, 13, 13, 0.25);
            box-shadow: 0px 4px 2px 0px rgba(14, 13, 13, 0.25);
            a{
                display: flex;
                flex-direction: column;
                justify-content: flex-start;
                width: 100%;
                height: 50vh;
                color: #000000;
                img{
                    width: 100%;
                    height: 60%;
                    object-fit: cover;
                }
                h1{
                    font-size: 6vw;
                    line-height: 7vw;
                    margin: 0;
                    padding: 4vw 4vw 0;
                    font-family: 'Titillium Web', sans-serif;
                    font-weight: 700;
                }
                div.description{
                    align-items: flex-start;
                    justify-content: flex-start;
                    height: 13%;
                    margin: 0;
                    padding: 0vw 4vw 0;
                    overflow: hidden;
                    flex-direction: column;
                    p,h1,h2,h3,h4{
                        padding: 0;
                        margin: 0;
                        font-family: 'Titillium Web', sans-serif;
                        font-weight: 300;
                        font-style: italic;
                        font-size: 4.3vw;
                        line-height: 5vw;
                    }
                }
                div{
                    display: flex;
                    height: 15%;
                    box-sizing: border-box;
                    justify-content: flex-end;
                    align-items: center;
                    span.rubrique_name{
                        img{
                            height: 5vw;
                            width: 5vw;
                            margin-right: 2vw;
                        }
                        height: 10vw;
                        border: 1px #fe6600 solid;
                        width: auto;
                        margin-right: 4vw;
                        text-align: center;
                        padding: 2vw 5vw;
                        font-size: 4vw;
                        text-transform: uppercase;
                        color: #fe6600;
                        font-family: 'Titillium Web', sans-serif;
                        font-weight: 600;
                        font-style: italic;
                        
                    }
                }
                
            }
        }
    
        .article{
            min-height: 100vh;
            background-color: white;
            h1{
                font-size: 10vw;
                line-height: 10vw;
                margin: 0;
                padding: 4vw 4vw 1vw;
                font-family: "Titillium Web",sans-serif;
                font-weight: 900;
                text-transform: uppercase;
                color: #fe6600;
            }
            li:before{
                content: "- ";
            }
            p, li{
                margin: 0;
                list-style: circle;
                padding: 0 4vw;
                font-family: "Titillium Web",sans-serif;
                font-weight: 300;
                font-style: italic;
                font-size: 5.5vw;
                line-height: 8vw;
                overflow: hidden;
                text-align: justify;
            }
            strong, h4{
                font-weight: 600;
                font-style: italic;
            }
            h2{
                font-size: 8vw;
                line-height: 6vw;
                margin: 6vw 0;
                padding: 4vw 4vw 0;
                font-family: "Titillium Web",sans-serif;
                font-weight: 700;
            }
            h3{
                font-size: 6vw;
                line-height: 6vw;
                margin: 0;
                padding: 4vw 4vw 0;
                font-family: 'Titillium Web', sans-serif;
                font-weight: 700;
            }
            .carousel{
                img{
                    height: 36vh;
                    padding: 0;
                    width: 100%;
                    object-fit: cover;
                }
            }
            img{
                height: auto;
                padding: 3vw 0;
                width: 100%;
                object-fit: contain;
            }
            a{
                font-weight: 600;
                font-style: italic;
                color: #fe6600;
            }
            blockquote{
                background-color: #c4f0d6;
                padding: 4vw 0;
                margin: 4vw 0;
                p{
                    border-left: 2vw #fe6600 solid;
                }
            }
        }
    }
    
    footer{
        background-color: black;
        padding: 3vw 0;
        margin-top: 10vw;
        ul{
            margin: 0;
            li{
                list-style: none;
                font-size: 6vw;
                line-height: 6vw;
                margin: 0;
                padding: 4vw 0;
                font-family: 'Titillium Web', sans-serif;
                font-weight: 700;
                color: white;
                margin: 0;
                a{
                    color :white
                }
            }
        }
    }
    
    form{
        h2{
            font-size: 8vw;
            line-height: 6vw;
            margin: 6vw 0;
            padding: 4vw 4vw 0;
            font-family: 'Titillium Web', sans-serif;
            font-weight: 700;
        }
        p, label{
            margin: 0;
            padding: 0vw 4vw 0;
            font-family: 'Titillium Web', sans-serif;
            font-weight: 300;
            font-style: italic;
            font-size: 5.5vw;
            line-height: 7vw;
            overflow: hidden;
            text-align: justify;
        }
        height: 80vh;
        width: 90%;
        margin: 0 auto;
    }
}

@media screen and (min-width: 500px){
    header{
        nav#desktop {
            display: flex;
            flex-wrap: wrap;
            -webkit-box-shadow: 0px 4px 2px 0px rgba(14, 13, 13, 0.25);
            -moz-box-shadow: 0px 4px 2px 0px rgba(14, 13, 13, 0.25);
            box-shadow: 0px 4px 2px 0px rgba(14, 13, 13, 0.25);
            z-index: 1;
            div{
                display: flex;
                width: 98%;
                padding: 0 1%;
                justify-content: space-between;
                div{
                    align-items: center;
                    width: 30%;
                    justify-content: center;
                    flex-wrap: nowrap;
                    display: flex;
                    a{  
                        width: 100%;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        text-decoration: none;
                        font-family: 'Titillium Web', sans-serif;
                        font-weight: 900;
                        list-style: none;
                        text-transform: uppercase;
                        font-size: 1.3vw;
                        color :black;
                        img.logo{
                            width: 5vw;
                            height: 5vw;
                        }
                        h1{
                            margin: 0;
                            font-size: 2vw;
                            font-family: "Titillium Web", sans-serif;
                            font-weight: 900;
                            text-transform: uppercase;
                        }
                        img{
                            width: 2vw;
                            height: 2vw;
                            margin: 0 1vw;
                        }
                    }
                    
                }
            }
            ul{
                width: 100%;
                padding: 0;
                margin: 0;
                display: flex;
                justify-content: space-around;
                z-index: 1;
                li{
                    flex-grow: 1;
                    padding: 0.5vw 0;
                    text-align: center;
                    font-family: 'Titillium Web', sans-serif;
                    font-weight: 900;
                    list-style: none;
                    text-transform: uppercase;
                    font-size: 1.5vw;
                    color :#fe6600;
                    box-sizing: inherit;
                }
                div{
                    align-items: baseline;
                    height: auto;
                    position: relative;
                    .displayed{
                        visibility: visible;
                        opacity: 1;
                    }
                    .not-displayed{
                        visibility: hidden;
                        opacity: 0;
                    }
                    ul{
                        transition: all .2s;
                        position: absolute;
                        background-color: white;
                        width: 100%;
                        display: block;
                        a{
                            color: black
                        }
                        li{
                            margin: 0;
                            font-weight: 600;
                            font-style: italic;
                            text-transform: capitalize;
                            outline: thick;
                            font-size: 1.3vw;
                            padding: 0.5vw 0;
                        }   
                    }
                }
            }
            
        }
        nav#mobile {
            display: none;
        }
    }
    
    main {
        min-height: 75vh;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        .title_block{
            width: 90%;
            margin: 0 auto;
            padding: 0 0 0.2vw;
            margin: 1vw auto 1vw;
            border-bottom: 0.8vw #fe6600 solid;
            h2{
                margin: 0;
                font-size: 2.5vw;
                font-family: 'Titillium Web', sans-serif;
                font-weight: 900;
                text-transform: uppercase;
                color :#fe6600
            }
            input{
                border: 0;
                background-color: transparent;
                font-size: 2.5vw;
                font-family: 'Titillium Web', sans-serif;
                height: 3vw;
                padding: 0;
                margin: 0;
                font-weight: 900;
                text-transform: uppercase;
                color :#fe6600
            }
            input::placeholder{
                font-size: 2.5vw;
                text-transform: uppercase;
                font-family: 'Titillium Web', sans-serif;
                font-weight: 900;
                color :#fe6600
            }
            input:focus{
                outline: none;
            }
        }
        
        img#banner-desktop{
            width: 100%;
            height: 80vh;
            object-fit: cover;
        }
        img#banner-mobile{
            display: none;
        }

        .article_list {
            width: 28.5%;
            height: 60vh;
            margin: 0.5vw;
            display: flex;
            flex-direction: column;
            background-color: white;
            align-items: center;
            -webkit-box-shadow: 0px 4px 2px 0px rgba(14, 13, 13, 0.25);
            -moz-box-shadow: 0px 4px 2px 0px rgba(14, 13, 13, 0.25);
            box-shadow: 0px 4px 2px 0px rgba(14, 13, 13, 0.25);
            a{
                display: flex;
                flex-direction: column;
                justify-content: flex-start;
                width: 100%;
                outline: none;
                text-decoration: none;
                height: 100%;
                color: #000000;
                img{
                    width: 100%;
                    height: 60%;
                    object-fit: cover;
                }
                h1{
                    font-size: 2vw;
                    line-height: 3vw;
                    margin: 0;
                    width: 100%;
                    padding: 1vw 1vw 0;
                    font-family: 'Titillium Web', sans-serif;
                    font-weight: 700;
                }
                div.description{
                    align-items: flex-start;
                    justify-content: flex-start;
                    height: 15%;
                    margin: 0;
                    padding: 0vw 1vw 0;
                    overflow: hidden;
                    flex-direction: column;
                    p,h1,h2,h3,h4{
                        padding: 0;
                        margin: 0;
                        font-family: 'Titillium Web', sans-serif;
                        font-weight: 300;
                        font-style: italic;
                        font-size: 1vw;
                        line-height: 1vw;
                    }
                }
                div{
                    display: flex;
                    height: 15%;
                    box-sizing: border-box;
                    justify-content: flex-end;
                    align-items: center;
                    span.rubrique_name{
                        img{
                            height: 1vw;
                            width: 1vw;
                            margin-right: 1vw;
                        }
                        border: 1px #fe6600 solid;
                        width: auto;
                        margin-right:2vw;
                        text-align: center;
                        padding: 0.5vw 2vw;
                        font-size: 1vw;
                        text-transform: uppercase;
                        color: #fe6600;
                        font-family: 'Titillium Web', sans-serif;
                        font-weight: 600;
                        font-style: italic;
                        
                    }
                }
                
            }
        }
    
        .article{
            z-index: 0;
            flex-grow: 1;
            background-color: white;
            h1{
                font-size: 3vw;
                line-height: 3vw;
                margin: 0;
                padding: 1vw 13vw 1vw;
                font-family: "Titillium Web",sans-serif;
                font-weight: 900;
                text-transform: uppercase;
                color: #fe6600;
            }
            p, li{
                margin: 0;
                padding: 0 13vw;
                font-family: "Titillium Web",sans-serif;
                font-weight: 300;
                font-style: italic;
                font-size: 1.3vw;
                line-height: 2vw;
                overflow: hidden;
                text-align: justify;
            }
            ul{
                padding: 1vw 0;
                margin-left: 15vw;
                li{
                    overflow: visible;
                    list-style: circle;
                    padding: 0;
                }
            }
            
            strong, h4{
                font-weight: 600;
                font-style: italic;
            }
            h2{
                font-size: 1.9vw;
                line-height: 4.6vw;
                margin: 0;
                padding: 1vw 13vw 0;
                font-family: "Titillium Web",sans-serif;
                font-weight: 700;
            }
            h3{
                font-size: 1.5vw;
                line-height: 2.5vw;
                margin: 0;
                padding: 1vw 13vw 0;
                font-family: "Titillium Web",sans-serif;
                font-weight: 700;
            }
            .carousel{
                margin: 0 auto;
                width: 100%;
                img{
                    height: 75vh;
                    width: 100%;
                    z-index: 0;
                    object-fit: cover;
                }
            }
            img{
                width: 100%;
                margin: 0 auto;
                z-index: 0;
                padding: 1vw 0;
                object-fit: contain;
            }
            a{
                font-weight: 600;
                font-style: italic;
                color: #fe6600;
            }
            blockquote{
                background-color: #c4f0d6;
                padding: 1vw 0;
                margin: 1vw 0;
                p{
                    border-left: 0.5vw #fe6600 solid;
                }
            }
        }
    }

    form{
        h2{
            font-size: 2vw;
            line-height: 2vw;
            margin: 2vw 0;
            padding:0;
            font-family: 'Titillium Web', sans-serif;
            font-weight: 700;
        }
        p, label{
            margin: 0;
            padding: 0;
            display: inline;
            font-family: 'Titillium Web', sans-serif;
            font-weight: 300;
            font-style: italic;
            font-size: 1.2vw;
            line-height: 1.2vw;
            overflow: hidden;
            text-align: justify;
        }
        width: 60%;
        margin: 0 auto;
    }

    footer{
        background-color: black;
        padding: 0.2vw 0;
        margin-top: 2vw;
        ul{
            margin: 0;
            li{
                list-style: none;
                font-size: 1vw;
                line-height: 1vw;
                margin: 0;
                padding: 0.5vw 0;
                font-family: 'Titillium Web', sans-serif;
                font-weight: 700;
                color: white;
                margin: 0;
                a{
                    color :white
                }
            }
        }
    }
    
    
}